<template lang="pug">
  .home(:style="{ backgroundImage: `url(${select.imageUrl})` }" v-if="!homeDetail" @click="homeDetail = true")
    .white-space White<br>Space
    ul.home-nav
      li(v-for="(room, index) in Rooms" :key="room.id" @click="goPath(room)" @mouseover="select = room; carousel = index" :class="{'active': select.id === room.id}") {{ room.name }}
    .home-name
      .home-name-index 0{{ Rooms.indexOf(select) + 1 }}
      .home-name-name {{ select.name }}
    .home-info
      .home-info-community.df
        .home-info-community-facebook
        .home-info-community-instagram
      .home-info-group
        .home-info-group-phone.df
          img(src="../assets/index_icon/phone-alt-solid.svg")
          p 02-17264937
        .home-info-group-email.df
          img(src="../assets/index_icon/envelope-solid.svg")
          p whitespace@whitespace.com.tw
        .home-info-group-address.df
          img(src="../assets/index_icon/home-solid.svg")
          p 台北市羅斯福路十段30號
  .homee-detail(v-else)
    .home-detail(:style="{ backgroundImage: `url(${select.imageUrl})` }")
      .white-space(@click="homeDetail=false") White<br>Space
      .home-info
        .home-info-community.df
          .home-info-community-facebook
          .home-info-community-instagram
        .home-info-group
          .home-info-group-phone.df
            img(src="../assets/index_icon/phone-alt-solid.svg")
            p 02-17264937
          .home-info-group-email.df
            img(src="../assets/index_icon/envelope-solid.svg")
            p whitespace@whitespace.com.tw
          .home-info-group-address.df
            img(src="../assets/index_icon/home-solid.svg")
            p 台北市羅斯福路十段30號
    .room-cards
      .card(v-for="(item, index) in Rooms" :key="index" @click="goPath(item)")
        .card-img(:style="{ backgroundImage: `url(${item.imageUrl})` }")
        .card-info
          .card-info-title {{ item.name }}
          .card-info-detail
            div
              span NT.{{ item.normalDayPrice }}
              span.fz12 平日
            div
              span.fz12 NT.{{ item.holidayPrice }}
              span.fz12 假日
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      select: {},
      isLoading: true,
      homeDetail: false,
      interval: null,
      carousel: 0
    }
  },
  methods: {
    goPath (item) {
      let id = item.id
      this.$router.push({ path: '/information', query: { id } })
    }
  },
  computed: {
    ...mapGetters(['Rooms'])
  },
  watch: {
    homeDetail (val) {
      if (val) {
        this.$nextTick(() => {
          let card = document.querySelector('.card')
          card.style.height = (card.clientWidth * 1.16) + 'px'
          document.querySelectorAll('.card').forEach((card, index, cards) => {
            if (index !== 0) card.style.height = cards[0].style.height
          })
          // onResize setcard height
          window.addEventListener('resize', function () {
            let card = document.querySelector('.card')
            if (!card) {
              window.removeEventListener('resize', this)
              return
            }
            card.style.height = (card.clientWidth * 1.16) + 'px'
            document.querySelectorAll('.card').forEach((card, index, cards) => {
              if (index !== 0) card.style.height = cards[0].style.height
            })
          })
          // Carousel
          this.interval = setInterval(() => {
            this.carousel = this.carousel < this.Rooms.length - 1 ? this.carousel + 1 : 0
            this.select = this.Rooms[this.carousel]
          }, 3000)
        })
      } else {
        if (this.interval) clearInterval(this.interval)
      }
    }
  },
  created () {
    this.$store.watch(state => {
      if (!state.isLoading) {
        this.select = this.Rooms[4]
      }
    })
  }
}
</script>
